.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.stars .wrapper {
  display: inline-block;
}
.stars .wrapper * {
  float:right; 
  color: rgba(83,91,160,1);
  width:150px;
  font-size: 30px;
}
.stars input {
  display: none;
}
.stars label{
  float:right;
  position: static !important; 
  line-height: normal;
  min-width:0px;
  font-size: 100px;
  padding: 0px;
  margin: 10px 0px;
}
.stars label::before{
  display: none;
  width: 0%;
}


.stars input:checked ~ label {
  font-size:100px;
}
.stars label:hover,
.stars label:hover ~ label {
  font-size:100px;
}
.stars input:checked ~ label:hover,
.stars input:checked ~ label:hover ~ label {
  font-size:100px;
}
.stars input[type=radio]:checked ~ label:after {
  font-size:100px;
}
.stars:hover input[type=radio]:checked ~ label:after,
.stars label:after {
  font-size:100px;
}
.stars label:after {
  font-size:100px;
}
.stars label:hover:after,
.stars label:hover ~ label:after {
  font-size:100px;
}

.in_wrapper{
  margin:50px auto;
}

.lecturer  .right-content{
